Vue.component('ScrollFaker', ScrollFaker);
Vue.component('SmartAction', SmartAction);
Vue.component('ElementTeleport', ElementTeleport);
Vue.component('ResizeableBox', ResizeableBox);
Vue.use(Cursor);
Vue.use(Request);
Vue.use(TippyTips);
Vue.use(Test);

